___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --background-color:#20819f;
  --text-color:#969393;
  --nav-icon-color: #fff;
}

.navigation{
    height: 60px;
    width: 100%;
    display: flex;
    align-items: center;
    position: relative;
    padding: 0.5rem 0rem;
    background-color: var(--background-color);
    color: var(--text-color);
}

.brand-name {
    text-decoration: none;
    color: #fff;
    font-size: 1.3rem;
    margin-left: 1rem;
  }

  .navigation-menu {
    margin-left: auto;
  }

  .navigation-menu ul {
    display: flex;
    padding: 0;
  }

  .navigation-menu li {
    list-style-type: none;
    margin: 0 1rem;
  }

  .navigation-menu li a {
    text-decoration: none;
    display: block;
    width: 100%;
    color: #fff;
  }

  .navigation-menu ul {
    display: flex;
    padding: 0;
  }
  .navigation-menu li {

    list-style-type: none;
    margin: 0 1rem;
  }
  .navigation-menu li a {

    text-decoration: none;
    display: block;
    width: 100%;
  }

  .hamburger {

    border: 0;
    height: 40px;
    width: 40px;
    padding: 0.5rem;
    border-radius: 50%;
    background-color: #d7d8dd;
    cursor: pointer;
    transition: background-color 0.2s ease-in-out;

    position: absolute;
    top: 50%;
    right: 25px;
    transform: translateY(-50%);
    display: none;
  }
  .hamburger:hover {
    background-color: #2642af;
  }


  @media screen and (max-width: 768px) {
    .hamburger {
      display: block;
    }
  }

  @media screen and (max-width: 768px) {
    .navigation-menu ul {
      display: none;
    }
  }

  @media screen and (max-width: 768px) {
    .navigation-menu.expanded ul {
      display: block;
    }
  }

  @media screen and (max-width: 768px) {
    .navigation-menu ul {
      position: absolute;
      top: 60px;
      left: 0;

      flex-direction: column;

      width: 100%;
      height: calc(100vh - 77px);
      background-color: white;
      border-top: 1px solid black;
    }
    .navigation-menu li {

      text-align: center;
      margin: 0;
    }
    .navigation-menu li a {
      color: black;

      width: 100%;
      padding: 1.5rem 0;
    }
    .navigation-menu li:hover {
      background-color: #eee;
    }
  }

  .navigationUnderline{
    border-bottom: 4px solid var(--background-color);
    border-radius: 10px;
  }

  .navigationUnderline1{
    border-bottom: 4px solid #d9d4d4;
    border-radius: 10px;
  }

  .navIconColor{
    color: var(--nav-icon-color);
  }

  .input-group-append{
    border: 5px solid var(--background-color);
    background-color: var(--background-color);
    border-radius: 5px;
  }
  
  .input-group-text{
    background-color: var(--background-color);
    border: var(--background-color);
  }
